//
// Site Defaults
html {
  background: $color_background;
  min-height: 100%;
  overflow-y: scroll;
}

body {
  margin: 0;
  font-family: $font_family_text;
  font-size: 90%;
    @include mq(toddler) {
        font-size: 100%;
    }
  line-height: $line_height;
  background-color: transparent;
  color: $color_text;
}
